// api/hotelBookingAPI.ts
export const fetchBookingData = async (startDate: string, endDate: string) => {
  const response = await fetch('/db.json'); // Ensure the path is correct
  const data = await response.json();

  // Convert arrival date to a single Date object for comparison
  const filteredBookings = data.bookings.filter((booking: any) => {
    const bookingDate = new Date(`${booking.arrival_date_year}-${booking.arrival_date_month}-${booking.arrival_date_day_of_month}`);
    return bookingDate >= new Date(startDate) && bookingDate <= new Date(endDate);
  });

  return filteredBookings;
};
