import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

interface DateRangeProps {
  onDateChange: (dates: [Date | undefined, Date | undefined]) => void;
}

const DateRangePicker: React.FC<DateRangeProps> = ({ onDateChange }) => {
  const [startDate, setStartDate] = useState<Date | undefined>(undefined);
  const [endDate, setEndDate] = useState<Date | undefined>(undefined);

  const handleChange = (dates: [Date | null, Date | null]) => {
    const [start, end] = dates;
    // Convert null to undefined for compatibility with DatePicker
    setStartDate(start ?? undefined);
    setEndDate(end ?? undefined);

    console.log("Selected start date:", start);
    console.log("Selected end date:", end);

    onDateChange([start ?? undefined, end ?? undefined]);
  };

  return (
    <DatePicker
      selected={startDate}
      onChange={handleChange}
      startDate={startDate}
      endDate={endDate}
      selectsRange
      inline
      isClearable
      placeholderText="Select a date range"
    />
  );
};

export default DateRangePicker;
