import React from 'react';
import Chart from 'react-apexcharts';
import { ApexOptions } from 'apexcharts'; // Import ApexOptions for proper typing

interface TimeSeriesChartProps {
  data: { x: string; y: number }[];
}

const TimeSeriesChart: React.FC<TimeSeriesChartProps> = ({ data }) => {
  // Define the options with proper type
  const options: ApexOptions = {
    chart: { id: 'time-series-chart' },
    xaxis: { type: 'datetime' }, // Correctly typed as 'datetime'
  };
  
  const series = [{ name: 'Visitors', data }];

  return <Chart options={options} series={series} type="line" height="350" />;
};

export default TimeSeriesChart;
