import React, { useEffect, useState } from 'react';
import DateRangePicker from './components/DatePicker';
import TimeSeriesChart from './components/Charts/TimeSeriesChart';
import ColumnChart from './components/Charts/ColumnChart';
import SparklineChart from './components/Charts/SparklineChart';
import { fetchBookingData } from './api/hotelBookingAPI';

const App: React.FC = () => {
  const [bookingData, setBookingData] = useState<any[]>([]);
  const [dateRange, setDateRange] = useState<[Date | undefined, Date | undefined]>([undefined, undefined]);

  useEffect(() => {
    if (dateRange[0] && dateRange[1]) {
      fetchBookingData(
        dateRange[0].toISOString().split('T')[0],
        dateRange[1].toISOString().split('T')[0]
      ).then(data => setBookingData(data));
    }
  }, [dateRange]);

  const timeSeriesData = bookingData.map(d => ({
    x: `${d.arrival_date_year}-${d.arrival_date_month}-${d.arrival_date_day_of_month}`,
    y: parseInt(d.adults) + parseInt(d.children) + parseInt(d.babies)
  }));

  const countryData = bookingData.reduce((acc, cur) => {
    acc[cur.country] = (acc[cur.country] || 0) + parseInt(cur.adults) + parseInt(cur.children) + parseInt(cur.babies);
    return acc;
  }, {} as Record<string, number>);

  return (
    <div>
      <h1>Hotel Booking Dashboard</h1>
      <DateRangePicker onDateChange={setDateRange} />
      <TimeSeriesChart data={timeSeriesData} />
      <ColumnChart data={Object.keys(countryData).map(key => ({ x: key, y: countryData[key] }))} />
      <SparklineChart data={bookingData.map(d => parseInt(d.adults))} title="Total Adults" />
      <SparklineChart data={bookingData.map(d => parseInt(d.children))} title="Total Children" />
    </div>
  );
};

export default App;
